export default "/*\r\n * Booking\r\n */\r\n\r\n.cover-container {\r\n    background-image: url('footer.jpg');\r\n    background-color: rgba(0, 0, 0, .7);\r\n    background-blend-mode: overlay;\r\n    background-size: cover;\r\n    background-position: center;\r\n    background-repeat: no-repeat;\r\n    height: 23rem;\r\n    padding-top: 10rem;\r\n    padding: 10rem 48px 3rem;\r\n}\r\n\r\n.cover-container h1 {\r\n    font-size: 50px;\r\n    font-weight: bold;\r\n    line-height: normal;\r\n    color: #fff;\r\n    text-transform: none;\r\n}\r\n\r\n/*\r\n * Contact\r\n */\r\n\r\n.contact-section {\r\n    background-color: #f3f3f3;\r\n    float: left;\r\n    width: 100%;\r\n    padding: 1.5rem 0;\r\n}\r\n\r\n.contact-section .section-heading {\r\n    padding: 48px;\r\n    float: left;\r\n    width: 100%;\r\n}\r\n\r\n.contact-section .section-heading h3 {\r\n    letter-spacing: 0.5em;\r\n    color: #8C8C8C;\r\n    font-size: 1.25rem;\r\n    margin: 0;\r\n    font-weight: 800;\r\n}\r\n\r\n.contact-section .section-heading .form-control {\r\n    border-radius: 0;\r\n    background-color: #f3f3f3;\r\n    color: #000;\r\n    border: 0;\r\n    border-bottom: 4px solid #bada55;\r\n    padding: 3px;\r\n    margin: 0;\r\n    text-overflow: ellipsis;\r\n    -webkit-appearance: none;\r\n    -moz-appearance: none;\r\n    appearance: none;\r\n    box-shadow: none;\r\n    margin-bottom: 1.25rem;\r\n    font-size: 1.5rem;\r\n    font-weight: 800;\r\n    resize: none;\r\n}\r\n\r\n.contact-section .section-heading .btn-green {\r\n    background-color: #9bcb6c;\r\n    border: 0;\r\n    cursor: pointer;\r\n    color: #000;\r\n    font-size: 1.5rem;\r\n    border-radius: 0;\r\n    font-weight: 800;\r\n    padding: 6px 20px;\r\n    height: 50px;\r\n    line-height: 40px;\r\n    width: 240px;\r\n    box-shadow: none;\r\n    margin-top: 1rem;\r\n}\r\n\r\n.section-testimonial {\r\n    float: left;\r\n    width: 100%;\r\n    padding: 0 48px;\r\n}\r\n\r\n.section-testimonial .section-heading {\r\n    border-top: 4px solid #ccc;\r\n    padding: 4rem 0;\r\n}\r\n\r\n.section-testimonial .section-heading h2 {\r\n    letter-spacing: 0.5em;\r\n    color: #8C8C8C;\r\n    font-size: 1.25rem;\r\n    margin: 0;\r\n    font-weight: 800;\r\n    margin-bottom: 2rem;\r\n}\r\n\r\n/* Bubble Slider */\r\n\r\n.bubble blockquote {\r\n    margin: 10px 10px 0;\r\n    background: #fff;\r\n    padding: 60px;\r\n    position: relative;\r\n    border: none;\r\n    border-radius: 8px;\r\n    font-size: 1.25em;\r\n    color: #000;\r\n}\r\n\r\n.bubble blockquote:before,\r\n.bubble blockquote:after {\r\n    content: \"\\201C\";\r\n    position: absolute;\r\n    font-size: 80px;\r\n    padding: 10px;\r\n    line-height: 1;\r\n    color: #000;\r\n}\r\n\r\n.bubble blockquote:before {\r\n    top: 0;\r\n    left: 10px;\r\n}\r\n\r\n.bubble blockquote:after {\r\n    content: \"\\201D\";\r\n    right: 10px;\r\n    bottom: -.5em;\r\n}\r\n\r\n.bubble div {\r\n    width: 0;\r\n    height: 0;\r\n    border-left: 0 solid transparent;\r\n    border-right: 20px solid transparent;\r\n    border-top: 20px solid #fff;\r\n    margin: 0 0 0 60px;\r\n    margin-bottom: 10px;\r\n}\r\n\r\n.bubble cite {\r\n    padding-left: 20px;\r\n    font-size: 1.25em;\r\n    color: #000;\r\n}\r\n\r\n@media screen and (max-width: 767px) {\r\n    .cover-container {\r\n        height: auto;\r\n        padding: 15rem 18px 3rem;\r\n    }\r\n    .cover-container h1 {\r\n        font-size: 33px;\r\n    }\r\n    .section-testimonial {\r\n        padding: 0 1.5rem;\r\n    }\r\n    .contact-section .section-heading {\r\n        padding: 4rem 1.5rem;\r\n    }\r\n}\r\n/*# sourceMappingURL=data:application/json;base64, */"